@import url(http://fonts.googleapis.com/css?family=Roboto:400,100,300,500,700,900);
/*HELPER*/
.clear {
  clear: both; }

.left {
  float: left; }

.right {
  float: right; }

.b1 {
  border: .1rem solid darkred !important; }

.b2 {
  border: .1rem solid navy !important; }

.b3 {
  border: .1rem solid lime !important; }

.none {
  display: none; }

