.grid {
  display: flex;
  flex-flow: row wrap;
  align-content: stretch;
  justify-content: stretch;
  align-items: flex-start;
  justify-content: space-between;
  align-content: stretch; }

/**/
/*PAGINATION*/
.pagination {
  text-align: center; }
  .pagination ul {
    display: flex;
    flex-flow: row wrap;
    align-content: stretch;
    justify-content: stretch;
    justify-content: center; }
    .pagination ul li {
      position: relative;
      margin: 0 -2px; }
      .pagination ul li a, .pagination ul li span {
        display: block;
        background: transparent;
        color: #0372AA;
        height: 30px;
        width: 30px;
        font-size: 1.4rem;
        line-height: 23px;
        font-weight: 700;
        cursor: pointer;
        transition: all 600ms;
        border: 4px solid #0372AA;
        border-radius: 100%; }
        .pagination ul li a:hover, .pagination ul li span:hover {
          background: transparent;
          color: #44d27e;
          border: 4px solid #44d27e; }
        .pagination ul li a i, .pagination ul li span i {
          margin-top: 3px; }
    .pagination ul .active {
      z-index: 99999; }
      .pagination ul .active a, .pagination ul .active span {
        color: #44d27e;
        background: transparent;
        border: 4px solid #44d27e; }
    .pagination ul .prev a, .pagination ul .next a {
      line-height: 22px !important; }

header {
  top: 0;
  left: 0;
  position: absolute;
  width: 100%;
  z-index: 99; }
  header .row {
    margin-bottom: 0; }
    header .row .col {
      padding: 0; }
  header .logo {
    margin: 20px 0;
    display: block;
    width: 100%;
    height: 60px;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat; }

.langs {
  top: 0px;
  right: 0px;
  position: absolute;
  font-size: 0;
  background: #44d27e;
  padding: 5px; }
  .langs span, .langs a {
    font-size: 1.3rem;
    line-height: 100%;
    font-weight: 400;
    display: inline-block;
    vertical-align: top;
    white-space: normal;
    vertical-align: middle; }
  .langs span {
    color: #f6f6f6;
    margin: 0 5px; }
  .langs a {
    color: #f6f6f6; }
    .langs a:hover {
      color: #fafafa; }
  .langs .active_idiomas {
    color: #fafafa; }

header.fixed {
  top: 20px;
  left: 0;
  position: absolute;
  width: 100%;
  z-index: 999; }
  header.fixed .logo {
    display: none; }

footer.fixed {
  position: absolute;
  bottom: 0;
  left: 0; }

footer {
  width: 100%;
  z-index: 99999999;
  color: #5a5a5a;
  background-image: url(../img/patterns/0.png);
  background-color: #f0f0f0;
  font-size: 1.4rem;
  line-height: 100%;
  font-weight: 400; }
  footer .social {
    text-align: center;
    margin: 20px 0; }
    footer .social a {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      text-align: center;
      height: 30px;
      width: 30px;
      border-radius: 5px;
      margin: 0 0 0 2px;
      background: #AAAAAA;
      transition: all 0.4s; }
      footer .social a:hover {
        background: #8c8c8c; }
        footer .social a:hover i {
          color: #f6f6f6; }
      footer .social a i {
        margin-top: 5px;
        color: #f6f6f6;
        font-size: 2rem;
        line-height: 100%;
        font-weight: normal;
        transition: all 600ms; }
  footer .text {
    color: #5a5a5a;
    font-size: 1.4rem;
    line-height: 150%;
    font-weight: 300; }
  footer .capa {
    background: #e6e6e6;
    padding: 10px 0; }
    footer .capa .row {
      margin-bottom: 0; }
  footer a {
    color: #44d27e; }
  footer .row {
    margin-bottom: 0; }
  footer figure {
    margin: 30px 0;
    width: 100%;
    text-align: center; }
    footer figure img {
      height: 50px;
      opacity: 0.6; }
  footer nav {
    margin: 45px 0 0;
    border-left: thin dotted #AAAAAA; }
    footer nav ul {
      width: 100%;
      text-align: center; }
      footer nav ul li {
        display: block;
        margin: 0 0px; }
        footer nav ul li a {
          padding: 10px 0px 10px 20px;
          text-align: left;
          text-transform: uppercase;
          display: block;
          transition: all 0.4s;
          color: #5a5a5a; }
          footer nav ul li a:hover {
            color: #44d27e; }
      footer nav ul .last {
        border: none !important; }
  footer .contact-info {
    width: 100%;
    margin: 10px 0 20px;
    text-align: center; }
    footer .contact-info > div {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      margin: 0 5px; }
      footer .contact-info > div i {
        margin-right: 5px; }
      footer .contact-info > div a {
        color: #5a5a5a;
        text-decoration: underline;
        transition: all 0.4s; }
        footer .contact-info > div a:hover {
          color: #44d27e; }
  footer .copyright {
    margin-top: 5px;
    display: block;
    color: #5a5a5a; }
  footer .ahorranito {
    text-align: right;
    margin-top: 3px; }
    footer .ahorranito a {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      color: #5a5a5a;
      vertical-align: middle; }
    footer .ahorranito .ahorranito-icon {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      vertical-align: middle; }

@media screen and (max-width: 960px) {
  footer .ahorranito, footer .copyright {
    margin-bottom: 10px;
    text-align: center;
    line-height: 120%; } }

@media screen and (max-width: 601px) {
  footer nav ul li a, footer .contact-info > div {
    line-height: 150%; } }

.ahorranito span {
  color: #1d9ad4; }
.ahorranito .ahorranito-icon {
  background-image: url(../img/ahorranito.png);
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  height: 20px;
  vertical-align: middle;
  width: 20px; }

